import React, { Component } from 'react';
import { withRouter } from 'react-router-dom'
import { Card, Input, TreeSelect, Button, message, Radio} from 'antd';
import {parseUrlSearch} from './util'
import {getAction,postAction } from '@/axios';
const { TreeNode } = TreeSelect;
class CreateForm extends Component {
    constructor(props){
        super(props);
        this.state = {
            name:"",
            folder:"",
            formUseType:1,//
            floderList:[],
            param:null
        }
    }
    handleChangeName = (e) => {
        this.setState({name:e.target.value})
    }
    handleChangeFolder = (value) => {
        this.setState({folder:value})
    }
    _changgeFormUseType = (e) => {
        this.setState({formUseType:e.target.value})
    }
    nextStep = () => {
        if(this.state.name===""){
            message.error("");
            return false
        }
        if(!this.state.folder){
            message.error("");
            return false
        }
        window.sessionStorage.setItem('formManageName',this.state.name);
        // console.log(this.state.name,this.state.folder)
        // idid
        if(this.state.param){
            // 
            let params={
                "formId": this.state.param.id,
                "formName": this.state.name,
                "formFolderId": this.state.folder,
                "formUseType":this.state.formUseType
            }
            postAction('/ybt/form/save',params).then(res=>{
                if(res.success){
                    message.success('')  
                    //          
                    this.props.history.push(`/formManagement/setWorkFlow?id=${this.state.param.id}`)         
                }else{
                    message.error(res.obj)   
                }
            })            
        }else{
            // 
            let params={
                "formName": this.state.name,
                "formFolderId": this.state.folder,
                "formUseType":this.state.formUseType
            }
            postAction('/ybt/form/save',params).then(res=>{
                if(res.success){
                    this.setState({
                        param:{
                            id:res.obj.formId
                        }
                    },()=>{
                        this.props.history.push(`/formManagement/setWorkFlow?id=${this.state.param.id}`)
                    })
                }else{
                    message.error(res.obj)  
                }
            })
            
            // if(res.success){
            //     let id=res.obj.id
            //     this.props.history.push(`/formManagement/setWorkFlow?id=${id}`)
            // }
        }
    }
    componentDidMount() {
        let param=parseUrlSearch()
        this.getFolderTree()
        if(param.id){
            this.setState({
                param
            },()=>{
                this.getData()
            })
        }
    }
    renderTree = (data=[]) => data.map(item => {
        if(item.children && item.children.length>0){
            return <TreeNode value={item.formFolderId} title={item.folderName} key={item.formFolderId}>
                {this.renderTree(item.children)}
            </TreeNode>
        }else{
            return <TreeNode value={item.formFolderId} title={item.folderName} key={item.formFolderId}></TreeNode>
        }
    })
    getFolderTree=()=>{
        getAction('/ybt/form/folder/queryFolderTree').then(res=>{
            if(res.success){                
                this.setState({
                    floderList:res.obj
                });  
            }else{
                message.error(res.obj)
            }
        });
    }
    getData=()=>{
        let {param}=this.state
        getAction('/ybt/form/info',{
            formId:param.id
        }).then(res=>{
            if(res.success){
                this.setState({
                    name:res.obj.formName || '',
                    folder:res.obj.formFolderId || '',
                    formUseType:Number(res.obj.formUseType) || 1
                })
            }
        });       
        
    }
    render() {
        return (
            <div style={{padding:20}}>
                <Card
                    title=""
                    bodyStyle={{height:"calc( 100vh - 300px )"}}
                >
                   <div></div>
                   <div style={{margin:"120px auto 0px auto",width:"500px"}}>
                        <div style={{marginBottom:20}}>
                            
                            <Input 
                                style={{width:400,marginLeft:30}} 
                                placeholder=""
                                value={this.state.name}
                                onChange={this.handleChangeName}
                            />
                        </div>
                        <div style={{marginBottom:20}}>
                            
                            <TreeSelect 
                                style={{width:400,marginLeft:30}}
                                dropdownStyle={{ maxHeight: 400, overflow: 'auto' }}
                                value={this.state.folder}
                                onChange={this.handleChangeFolder}
                                // placeholder={''}
                            >
                                {
                                    this.renderTree(this.state.floderList)
                                }
                            </TreeSelect>
                        </div>
                        <div style={{marginBottom:40}}>
                            
                            <Radio.Group onChange={this._changgeFormUseType} value={this.state.formUseType} style={{marginLeft:30}} >
                                <Radio value={1}></Radio>
                                <Radio value={2}></Radio>
                                <Radio value={3}></Radio>
                            </Radio.Group>
                        </div>
                        <div style={{textAlign:"center"}}>
                            <Button type="primary" style={{margin:"0 20px"}} onClick={this.nextStep}></Button>
                            <Button onClick={()=>{this.props.history.push("/formManagement")}}></Button>
                        </div>
                   </div>
                </Card>
            </div>
        )
    }
}

export default withRouter(CreateForm)